<template>

  <svg id="Dymo" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="-2 -2 500 25">

      <filter id="f5" width="300%" height="200%">
        <feOffset result="offOut" in="SourceGraphic" dx="0" dy="-0.1" />
        <feColorMatrix result="matrixOut" in="offOut" type="matrix"
        values="0.2 0 0 0 0 0 0.2 0 0 0 0 0 0.2 0 0 0 0 0 1 0" />
        <feGaussianBlur result="blurOut" in="matrixOut" stdDeviation="0.5" />
        <feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
      </filter>

      <g style="filter: url(#f5);">
        <svg :width="stripLength" viewBox="0 0 100 50" preserveAspectRatio="none">
          <rect x="0" y="0" width="90" height="40" :fill="fill"/>
        </svg>
      </g>

      <g style="opacity: 0.8;">

        <filter id="MyFilter" filterUnits="userSpaceOnUse" width="2000%" height="2000%">
          <feGaussianBlur in="SourceAlpha" stdDeviation="4" result="blur"/>

          <feSpecularLighting in="blur" surfaceScale="10" specularConstant=".75"
                              specularExponent="15" lighting-color="#ffffff"
                              result="specOut">
            <fePointLight x="-5000" y="-10000" z="20000"/>
          </feSpecularLighting>
          <feGaussianBlur in="SourceGraphic" stdDeviation="1" result="SourceGraphicBlur"/>
          <feComposite in="specOut" in2="blur" operator="in" result="specOut"/>
          <feComposite in="SourceGraphicBlur" in2="specOut" operator="arithmetic"
                       k1="0" k2="1" k3="1" k4="0" result="litPaint"/>
          <feMerge>
            <feMergeNode in="blur"/>
            <feMergeNode in="litPaint"/>
          </feMerge>
        </filter>

        <g transform="scale(0.22)">
          <text id="LabelTextText" :x="margin/0.22" y="65" style="font-size: 59.43196105957031px;fill: #fff;stroke: #fff;stroke-miterlimit: 10;stroke-width: 2px;font-family: BrysoLetterGothicStd-Medium, Bryso Letter Gothic Std;letter-spacing: 0.23700012284923194em;filter: url(#MyFilter);">{{displayText}}</text>
        </g>
      </g>


  </svg>
</template>

<script>
import tinycolor from 'tinycolor2'
export default {
  name: 'Dymo',
  components: {
  },
  props: {
    fill: {
      default:'#0000ff'
    },
    displayText: {
      default:'DYMO style'
    }
  },
  data () {
    return {
      margin:17
    }
  },
  methods: {

  },
  computed: {
    stripLength: function (){
      //return ((this.displayText.length*1.237) * 20.57);
      return (this.displayText.length*20.57 + (this.displayText.length-1)*20.57*0.237)/2.1 + 2*this.margin;
    }
  },
  watch: {

  },
  mounted: function () {

  }
}


</script>

<style>

/* BrysoLetterGothicStd */
@font-face {
  font-family: BrysoLetterGothicStd-Medium;
  src: url(data:application/x-font-woff;charset=utf-8;base64,);
}

</style>



